import React from "react";
import Xpanel from "./Xpanel";
import Footer from "./Footer";
//import moment from "moment"



class App extends React.Component{

    state={
            items:[],
            
            
    };

    onadd=(tempobj)=>{
        //console.log("this is from app" + tempobj)
        //  let newitems=[...this.state.items]
        //  newitems=newitems.push(tempobj)
         this.setState({
             items:[...this.state.items,{tempobj}]          //syntax is [ealier,what we have to push]
                       })
         //console.log("pushed")
         //console.log(this.state)
    }

    ondelete=(key)=>{
        //console.log("ondelete is called")
        console.log(key)
        let items=[...this.state.items];
        //console.log(items)
        
        //[ items ]= this.state.items.tempobj;

        //console.log(items[key])

        // console.log("items")
        // console.log(items)

        
        if (key > -1) {
            items.splice(key, 1);
        }

        this.setState({
            items
        })

    }


    render()
    {
        let sum=0;
        return(
            <div>
                <nav className="navbar navbar-dark bg-dark">
                    {
                        //sum=0;
                        this.state.items.map(item=>{
                            //console.log(item)
                        sum=sum+item.tempobj.itemcost;
                        //console.log(sum)
                        return console.log();
                    })}
                    

                <span className="navbar-brand mb-0 h1">Xpense&trade;</span>
                <span className="navbar-brand mb-0 h1" id="headingTotal">{sum}</span>    
		        </nav>

                    
                <div className="container-fluid bg-light" style={{minheight: "100vh"}}>{/*content outer*/}
                        <div className="container">

                        <Xpanel onadd={this.onadd}/>

                        {/*content below */}
                        <ul className="list-group">

                        {this.state.items.map((item,index)=>{
                            //console.log(item.tempobj)
                            //cont
                            return(
                            <li id={item.tempobj.timestampkey} key={item.tempobj.timestampkey} className="list-group-item d-flex justify-content-between">
							<div className="d-flex flex-column">
								{item.tempobj.itemname}
                        <small className="text-muted">{Date(item.tempobj.timestampkey)}</small>
							</div>
							<div>
								<span className="px-5">
									{item.tempobj.itemcost}
								</span>
                                {/*this is how you prevent function from calling*/}
								<button type="button" onClick={()=> this.ondelete(index)  } className="btn btn-outline-danger btn-sm">
									<i className="fas fa-trash-alt"></i>
								</button>
							</div>
                        </li>
                        );
                            //cont
                        })}
						</ul>                        



                        </div>




                </div>
                <br />
                <Footer />

            </div>
        );
    }


}

export default App;